  static socket = null; // Add a static property to hold the socket connection
  static dauberInstance = null; // Add static property to hold the Dauber instance
  static cardDrawerInstance = null; // Add static property to hold the CardDrawer instance
  static blowerInstance = null; // Add static property to hold the Blower instance
