  // Moved initialization logic into a separate method
  static initializeGameComponents() {
    if (!Initializer.config) {
      console.error("CLIENT: Config not loaded before initializing components.");
      return;
    }
    const conf = Initializer.config;

    console.log("CLIENT: DOMContentLoaded fired, initializing components...");

    Initializer.setTitle(conf.gameConf.appTitle);
    Initializer.winningDialogInstance = Initializer.addWinningDialog(conf.gameConf.winningDialog); // Ensure instantiation happens
    new FlyingPrize(); // Instantiate FlyingPrize to activate its PRIZE_WON listener
    Initializer.setCardPrices(conf.gameConf.cardPrice);
    Initializer.addWinPatternAnimModule(conf.gameConf.winPatternsAnimModule);
    Initializer.attachEnoughBalanceListener(Initializer.addMarketPlace(conf.gameConf.marketCards), conf, document); // Keep this for UI flow?
    Initializer.addStartButton(conf, Initializer.addMarketPlace(conf.gameConf.marketCards));
    Initializer.dauberInstance = Initializer.addDauber(conf); // Store the created Dauber instance
    Initializer.addLogoutBtn();
    Initializer.showUserInfo(); // Ensure user info is shown before trying to get username

    // Connect WebSocket *before* creating CardDrawer so socket exists
    Initializer.connectWebSocket();

    // Pass socket instance to CardDrawer - it will need it to pass to Card instances
    // Ensure socket is connected before creating CardDrawer? Or handle potential null socket in CardDrawer/Card?
    // For now, assume connectWebSocket establishes Initializer.socket synchronously enough or Card handles null.
    // A better approach might involve awaiting connection or passing socket later.
    Initializer.orientationManagerInstance = new OrientationManager(); // Instantiate OrientationManager

    Initializer.addBingoListener(); // Add listener for local BINGO events
    Initializer.addLoginAndRegisterListeners(); // Add listeners for login and registration
    Initializer.addPiLoginButtonListener(); // Add listener for Pi Login
   }
  
   static addLoginAndRegisterListeners() {
    const loginForm = document.querySelector('#loginForm');
    const registerForm = document.querySelector('#registerForm');
    const signInLink = document.querySelector('.sign-in-link a'); // Link to toggle forms
    const alertMsg = document.querySelector('#alertMsg'); // For hiding error messages on form toggle

    // Removed debug logs
    if (loginForm) {
      loginForm.addEventListener('submit', (event) => {
        event.preventDefault();
        console.log('CLIENT: Login form submitted via JS.');
        ApiController.login();
      });
    } else {
      console.warn("CLIENT: Login form (#loginForm) not found.");
    }

    if (registerForm) {
      registerForm.addEventListener('submit', (event) => {
        event.preventDefault();
        console.log('CLIENT: Register form submitted via JS.');
        ApiController.register();
      });
    } else {
      console.warn("CLIENT: Register form (#registerForm) not found.");
    }

    if (signInLink && loginForm && registerForm) {
      // Removed debug log
      signInLink.addEventListener('click', (event) => {
        // Removed debug log
        event.preventDefault();
        const isLoginFormVisible = loginForm.style.display !== 'none';
        // Removed debug log
        if (isLoginFormVisible) {
          loginForm.style.display = 'none';
          registerForm.style.display = 'block';
          signInLink.innerHTML = '<span class="icon0-question-sign icon0-white"></span> Already have an account? Login here!';
        } else {
          loginForm.style.display = 'block';
          registerForm.style.display = 'none';
          signInLink.innerHTML = '<span class="icon0-question-sign icon0-white"></span> Don\'t have an account? Register here!';
        }
        // Hide any previous error messages when toggling forms
        if (alertMsg) {
          alertMsg.style.display = 'none';
        }
      });
    } else {
      console.warn("CLIENT: Sign-in link or forms not found for toggling.");
    }
   }
  
   static addPiLoginButtonListener() {
    const piLoginBtn = document.querySelector('#piLoginBtn');
    if (piLoginBtn) {
    	// Show the Pi login button if the Pi SDK seems to be available
    	if (typeof Pi !== 'undefined' && Pi.authenticate) {
    		piLoginBtn.style.display = 'block'; // Or 'inline-block' depending on styling
    		piLoginBtn.addEventListener('click', (event) => {
    			event.preventDefault();
    			console.log('CLIENT: Pi Login button clicked.');
    			ApiController.loginWithPi();
    		});
    	} else {
    		console.warn("CLIENT: Pi SDK not detected, Pi Login button will remain hidden.");
    	}
    } else {
    	console.warn("CLIENT: Pi Login button (#piLoginBtn) not found.");
    }
   }
  
   static applyConfigurations(conf) {
    Initializer.config = conf; // Store config
    // Wait for DOM to be ready before initializing components that interact with it
    if (document.readyState === 'loading') {
      document.addEventListener('DOMContentLoaded', Initializer.initializeGameComponents);
    } else {
      // DOM is already ready
      Initializer.initializeGameComponents();
    }
  }

  static setTitle(appTitle) {
    document.querySelector('title').innerText = appTitle;
  }

