  static addStartButton(conf, elMarketPlace) {
    let startBtn = undefined;
    if (conf.gameConf.mainGame) {
      startBtn = document.querySelector('#startBtn');
      if (startBtn) {
        // Make the event listener async to use await
        startBtn.addEventListener('click', async () => {
          // --- Balance Check START ---
          const marketCardsInstance = new MarketCards(elMarketPlace); // Need instance for getRadioButtonsArray
          const purchasedCardsCount = MarketCards.getPurchasedCardsCount(marketCardsInstance.getRadioButtonsArray());
          const cardPrice = conf.gameConf.cardPrice;
          const totalCost = purchasedCardsCount * cardPrice;

          try {
            const currentBalance = await ApiController.getPlayerBalancePromise();

            if (Number(currentBalance) < totalCost) {
              alert(`Insufficient balance. You need ${totalCost}, but you only have ${currentBalance}.`);
              console.warn("CLIENT: Start game blocked due to insufficient balance.");
              return; // Stop processing if balance is too low
            }
            // --- Balance Check END ---

            // If balance is sufficient, proceed:
            console.log("CLIENT: Balance sufficient, proceeding with game start.");
            // Buy cards (deducts balance) and render them
            Initializer.buyCards(conf, elMarketPlace);

            // Get the generated card data from the drawer instance
            const cardData = Initializer.cardDrawerInstance ? Initializer.cardDrawerInstance.getCardData() : null;
            if (!cardData) {
                console.error("CLIENT: Could not retrieve card data after generation. Aborting game start.");
                alert("Error preparing cards. Please try again.");
                // Re-enable button?
                startBtn.disabled = false;
                startBtn.innerText = 'START GAME';
                return;
            }

            // Show waiting message and disable button
          const timerContainer = document.querySelector('#timerContainer');
          if (timerContainer) {
              // Assume this player makes 1 ready player initially
              const message = `<p>Waiting for players... 1 / 2 ready</p>`;
              console.log("CLIENT: Setting initial waiting message:", message);
              timerContainer.innerHTML = message;
              ViewManipulator.toggleVisibility(timerContainer, true); // Show immediately
          } else {
              console.warn("CLIENT: #timerContainer not found on start button click.");
          }
          startBtn.disabled = true;
          startBtn.innerText = 'Waiting...';

          // Then, tell the server, including the card data
          if (Initializer.socket) {
            // Get username reliably from profile info
            const profile = ApiController.getProfileInfo();
            const username = profile?.name;
            if (!username) {
                console.error("CLIENT: Cannot start game, user profile name not found.");
                alert("Error: User information not found. Cannot start game.");
                // Re-enable button?
                startBtn.disabled = false;
                startBtn.innerText = 'START GAME';
                return;
            }
            console.log(`CLIENT: Emitting startGame event for user: '${username}' with cards:`, cardData); // Log emit
            Initializer.socket.emit('startGame', { username: username, cards: cardData }); // Send reliable username AND cards
          }
        } catch (error) {
            alert("Could not verify balance. Please try again.");
            console.error("CLIENT: Error checking balance before starting game:", error);
            // Optionally re-enable the button here if needed
        }
        });
      }
    }
    return startBtn;
  }

